import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../../environments/environment';
import {slideData} from '../onair/onair.component';

@Injectable({
  providedIn: 'root'
})
export class OnairService {
  private apiUrl = environment.apiUrl;

  constructor(
    private http: HttpClient,
  ) { }

  // get selected list
  getSlides () {
    return this.http.get<any>(this.apiUrl + 'control/onair/slides');
  }

  //add slide data
  addSlideData(data: slideData) {
    return this.http.post<any>(this.apiUrl + 'control/onair/slide/data', data);
  }

  //add slide data
  deleteSlideData(id: number) {
    return this.http.delete<any>(this.apiUrl + 'control/onair/slide/data/'+id);
  }

  //activate slide
  activateSlide(data: { slide_id:number, active_slide_id: number }) {
    return this.http.post<any>(this.apiUrl + 'control/onair/slide/activate', data);
  }
}
