import {Component, OnInit, ViewChild} from '@angular/core';
import {Ticker, TickersNews} from "../../news/models/news";
import {Store} from "@ngrx/store";
import {Actions, ofType} from "@ngrx/effects";
import {SessionService} from "../../shared/services/session.service";
import * as newsActions from "../../news/store/news.actions";
import * as controlActions from "../store/control.actions";
import {AddTickerDialogComponent} from "../../news/add-ticker-dialog/add-ticker-dialog.component";
import {AddTickerNewsDialogComponent} from "../../shared/component/add-ticker-news-dialog/add-ticker-news-dialog.component";
import {ConfirmDialogComponent} from "../../shared/component/confirm-dialog/confirm-dialog.component";
import { MatDialog } from "@angular/material/dialog";
import { MatSidenav } from "@angular/material/sidenav";
import {ControlState} from "../../store/app.state";

@Component({
  selector: 'app-ticker',
  templateUrl: './ticker.component.html',
  styleUrls: ['./ticker.component.css']
})
export class TickerComponent implements OnInit {

  tickers: Ticker[];
  selectedTicker: Ticker;

  sidePanelOpened = true;
  sidePanelOpened_noNews = true;

  @ViewChild('sidebar') sidenav: MatSidenav;
  selectedNews: any;

  constructor(
    private store: Store<any>,
    public dialog: MatDialog,
    private updates$: Actions,
    private sessionService: SessionService
  ) { }

  ngOnInit() {
    /*this.store.dispatch(new newsActions.UpdateNewsLoaded({ isLoaded: true }));*/
    this.store.dispatch(new controlActions.GetTickers());

    // update selected list selection
    this.updates$
      .pipe(ofType(controlActions.GET_TICKERS_SUCCESS))
      .subscribe(() => {
        let sub = this.store.select(control_store => control_store.ControlState.tickers)
          .subscribe(tickers => {
            this.tickers = tickers;
          });
      });
  }

  onSelectTicker(ticker: Ticker) {
    if (this.selectedTicker && this.selectedTicker.id === ticker.id) {
      this.selectedTicker = null;
    }else {
      this.selectedTicker = ticker;
    }
  }

  onAddTicker() {
    let dialogRef = this.dialog.open(AddTickerDialogComponent, { width: '25%' });

    dialogRef.componentInstance.onAdd.subscribe((result: any) => {
      // this.updates$
      // .ofType(newsActions.ADD_TICKER_SUCCESS)
      // .subscribe(() => {
      //   let sub = this.store.select(news_store => news_store.NewsState.tickers)
      //   .subscribe(tickers => {

      // console.log('TICKER ADDED', tickers)
      //     this.tickers = tickers;
      //   });
      // });
      this.UpdateTickerSelection(controlActions.ADD_TICKER_SUCCESS, true);
    });
  }

  onAddTickerNews() {
    let dialogRef = this.dialog.open(AddTickerNewsDialogComponent, { width: '95%', data: { ticker: this.selectedTicker, source: 'control' } });

    dialogRef.componentInstance.onAdd.subscribe((result: any) => {
      this.UpdateTickerSelection(controlActions.ADD_TICKER_NEWS_SUCCESS);
    });
  }

  onApproveTickerNews(t_news_id: number) {
    this.store.dispatch(new controlActions.ApproveTickerNews({ ticker_news_id: t_news_id }));

    this.UpdateTickerSelection(controlActions.APPROVE_TICKER_NEWS_SUCCESS);
  }

  onDisApproveTickerNews(t_news_id: number) {
    this.store.dispatch(new controlActions.DisApproveTickerNews({ ticker_news_id: t_news_id }));

    this.UpdateTickerSelection(controlActions.DISAPPROVE_TICKER_NEWS_SUCCESS);
  }

  onDeleteTickerNews(t_news_id: number) {
    let dialogRef = this.dialog.open(ConfirmDialogComponent,
      { width: '350px', data: {title: 'Delete Ticker News', message: 'Are you sure you want to delete this news from ticker?'} });

    dialogRef.afterClosed().subscribe((result: string) => {
      dialogRef = null;
      if (result) {
        this.store.dispatch(new controlActions.DeleteTickerNews({ ticker_news_id: t_news_id }));
        this.UpdateTickerSelection(controlActions.DELETE_TICKER_NEWS_SUCCESS);
      }
    });
  }

  UpdateTickerSelection(newsAction, reloadTickers:boolean = false) {
    this.updates$
      .pipe(ofType(newsAction))
      .subscribe(() => {
        let sub = this.store.select(news_store => news_store.ControlState.tickers)
          .subscribe(list => {
            if(reloadTickers) {
              this.tickers = list;
            }
            if (this.selectedTicker) {
              list.forEach((element: TickersNews) => {
                if (element.id === this.selectedTicker.id) {
                  this.selectedTicker = element
                }
              });
            }
          });

      });
  }

  checkRight() {
    if (this.sessionService.checkRole('ticker-editor')) {
      return true;
    } else if (this.sessionService.checkRole('news-editor')) {
      return true;
    }
    return false;
  }

  sideBarToggle() {
    this.sidenav.toggle();
  }

  isOver(): boolean {
    return window.matchMedia(`(max-width: 960px)`).matches;
  }

}
