import { Component, OnInit, ViewChild } from '@angular/core';
import { MatSidenav } from '@angular/material/sidenav';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog } from '@angular/material/dialog';
import { ControlService } from '../services/control.service';
import { OnairService } from '../services/onair.service';
import { SlideDataDialogComponent } from '../slide-data-dialog/slide-data-dialog.component';

import { Subject } from 'rxjs/Subject';
import { takeUntil } from 'rxjs/operators';
import { Language } from '../../news/models/news';
import {ConfirmDialogComponent} from "../../shared/component/confirm-dialog/confirm-dialog.component";
import * as controlActions from "../store/control.actions";
import {MatCheckboxChange} from "@angular/material/checkbox";

@Component({
  selector: 'app-onair',
  templateUrl: './onair.component.html',
  styleUrls: ['./onair.component.css']
})
export class OnairComponent implements OnInit {

  sidePanelOpened = true;
  sidePanelOpened_noNews = true;

  slides: Slide[];
  selectedSlide: Slide;
  settings_selected_slide: number;
  destroySubject$: Subject<void> = new Subject();

  bulkSelected = { "items": [] };
  bulkSelectBtn = false;

  @ViewChild('sidebar') sidenav: MatSidenav;

  constructor(
    private onAirService: OnairService,
    public dialog: MatDialog,
    public snackBar: MatSnackBar,
  ) { }

  ngOnInit() {
    this.onAirService.getSlides().subscribe(onair => {
      this.slides = onair;
      console.log(this.slides);
    });
  }



  sideBarToggle() {
    this.sidenav.toggle();
  }

  isOver(): boolean {
    return window.matchMedia(`(max-width: 960px)`).matches;
  }

  onSelectSlide(slide:Slide){
    this.selectedSlide = slide;
    this.settings_selected_slide = slide.id;
    /*console.log(slide)*/
    this.bulkSelected.items = [];
    this.bulkSelectBtn = false;
  }

  addSlideData(slideId: number) {
    let content = new slideData(null, new Language('Dhv'), this.settings_selected_slide, 0);

    let dialogRef = this.dialog.open(SlideDataDialogComponent, { width: '40%', height: 'auto', maxHeight: '95%', data: content });

    dialogRef.componentInstance.onAdd.subscribe((result: slideData) => {
      console.log(result)
      this.onAirService.addSlideData(result).subscribe(re => {
        console.log(re)
        this.slides = re;
        this.slides.forEach(data => {
          if(data.id === this.settings_selected_slide) {
            this.selectedSlide = data;
          }
        })
      })
    },
    err=>{
      console.log("status code--->"+err.status);
    });
  }

  deleteContent(id: number) {

    var dialogRef = this.dialog.open(ConfirmDialogComponent,
      { width: '350px', data: {title: 'Delete Ticker News', message: 'Are you sure you want to delete this?'} });

    dialogRef.afterClosed().subscribe((result: string) => {
      dialogRef = null;
      if (result) {

        // delete
        this.onAirService.deleteSlideData(id).subscribe(re => {

            this.onAirService.getSlides().subscribe(onair => {
              this.slides = onair;

              this.slides.forEach(data => {
                if(data.id === this.settings_selected_slide) {
                  this.selectedSlide = data;
                }
              })
            });
          },
          err=>{
            console.log("Err Deleting Slide Data, status code--->"+err.status);
          });

      }
    });

  }

  activate(id: number) {
    this.onAirService.activateSlide({slide_id: this.selectedSlide.id, active_slide_id: id}).subscribe(re => {
      console.log(re)
      this.slides = re;
      this.slides.forEach(data => {
        if(data.id === this.settings_selected_slide) {
          this.selectedSlide = data;
        }
      })
    })
  }


  bulkSelection($event: MatCheckboxChange, id: number) {
    if (!this.bulkSelected.items.includes(id) && $event.checked)
      this.bulkSelected.items.push(id);

    if (!$event.checked) {
      this.bulkSelected.items.forEach((value,index)=>{
        if(value==id) this.bulkSelected.items.splice(index,1);
      });
    }
  }

  selectAll($event: MatCheckboxChange) {
      for (let i = 0; i < this.selectedSlide.onairslidecontent.length; i++) {
        if (!this.selectedSlide.onairslidecontent[i].active) {
          this.selectedSlide.onairslidecontent[i].selected = $event.checked;
          this.bulkSelected.items.push(this.selectedSlide.onairslidecontent[i].id);
        }

        if (!$event.checked) {
          this.bulkSelected.items = [];
        }

      }
  }
}




export class Slide {
  id: number;
  slide_id: string;
  slide_name: string;
  onairslidecontent: slideData[];
}

export class slideData{
  id?: number;
  value?: string;
  data: string;
  language: Language;
  on_air_slides_id: number;
  active: number;
  created_at?: string;
  selected?: boolean = false;

  constructor(data?: string, lang?: Language, s_id?: number, active?: number) {
    this.data     = data;
    this.language = lang;
    this.on_air_slides_id = s_id;
    this.active   = active;
  }
}
