import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Action } from '@ngrx/store';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/catch';
import {Actions, Effect, ofType} from '@ngrx/effects';
import { of } from 'rxjs/observable/of';
import { map } from 'rxjs/operators';

import * as ControlActions from './control.actions';
import { ControlService } from '../services/control.service';
import * as NewsActions from "../../news/store/news.actions";
import {NewsService} from "../../news/services/news.service";

@Injectable()
export class ControlEffects {

  constructor(
    private actions$: Actions,
    private controlService: ControlService,
    private newsService: NewsService,
  ) { }

  @Effect()
  GetNewsCgNews$: Observable<Action> = this.actions$.
    pipe(ofType<ControlActions.GetNewsCgNews>(ControlActions.GET_NEWS_CG_NEWS))
    .mergeMap(action =>
      this.controlService.getNewsCgNews()
      .map((data) => {
        console.log(data);
        return new ControlActions.GetNewsCgNewsSuccess(data);
      })
      .catch(() => of(new ControlActions.GetNewsCgNewsError()))
    )



  /*GetCgSettings$: Observable<Action> = this.actions$.
    ofType<ControlActions.GetNewsCgNews>(ControlActions.GET_NEWS_CG_NEWS)
    .mergeMap(action =>
      this.controlService.getNewsCgSettings()
      .map((data) => {
        console.log(data);
        return new ControlActions.GetCgSettingsSuccess(data);
      })
      .catch(() => of(new ControlActions.GetCgSettingsError()))
    );*/


  @Effect()
  UpdateNewsCgNews$: Observable<Action> = this.actions$.
    pipe(ofType<ControlActions.UpdateNewsCgNews>(ControlActions.UPDATE_NEWS_CG_NEWS))
    .mergeMap(action =>
      this.controlService.updateNewsCgText(action.payload.news_bar_id)
      .map((data) => {
        console.log(data);
        return new ControlActions.UpdateNewsCgNewsSuccess();
      })
      .catch(() => of(new ControlActions.UpdateNewsCgNewsError()))
    );


    @Effect()
    StopNewsCg$: Observable<Action> = this.actions$.
      pipe(ofType<ControlActions.UpdateNewsCgNews>(ControlActions.STOP_NEWS_CG))
      .mergeMap(action =>
        this.controlService.stopNewsCg()
        .map((data) => {
          console.log(data);
          return new ControlActions.StopNewsCgSuccess();
        })
        .catch(() => of(new ControlActions.StopNewsCgError()))
      );



  /*----------------------------------------------------------------------------------------*/
  /*---------------------------------------- Ticker ----------------------------------------*/
  /*-----------------------------------------START------------------------------------------*/
  // get tickers, returned with ticker's news
  @Effect()
  GetTickers$: Observable<Action> = this.actions$.
  pipe(ofType<ControlActions.GetTickers>(ControlActions.GET_TICKERS))
    .mergeMap(action =>
      this.newsService.GetTickers()
        .map((data) => {
          return new ControlActions.GetTickersSuccess(data);
        })
        .catch(() => of(new ControlActions.GetTickersError()))
    );

  // add a news to ticker
  @Effect()
  AddTickerNews$: Observable<Action> = this.actions$.
  pipe(ofType<ControlActions.AddTickerNews>(ControlActions.ADD_TICKER_NEWS))
    .mergeMap(action =>
      this.newsService.AddTickerNews(action.payload)
        .map((data) => {
          return new ControlActions.AddTickerNewsSuccess(data);
        })
        .catch(() => of(new ControlActions.AddTickerNewsError()))
    );

  // approve a news to ticker
  @Effect()
  ApproveTickerNews$: Observable<Action> = this.actions$.
  pipe(ofType<ControlActions.ApproveTickerNews>(ControlActions.APPROVE_TICKER_NEWS))
    .mergeMap(action =>
      this.newsService.ApproveTickerNews(action.payload.ticker_news_id)
        .map((data) => {
          return new ControlActions.ApproveTickerNewsSuccess(data);
        })
        .catch(() => of(new ControlActions.ApproveTickerNewsError()))
    );

  // dis-approve a ticker news
  @Effect()
  DisApproveTickerNews$: Observable<Action> = this.actions$.
  pipe(ofType<ControlActions.DisApproveTickerNews>(ControlActions.DISAPPROVE_TICKER_NEWS))
    .mergeMap(action =>
      this.newsService.DisApproveTickerNews(action.payload.ticker_news_id)
        .map((data) => {
          return new ControlActions.DisApproveTickerNewsSuccess(data);
        })
        .catch(() => of(new ControlActions.DisApproveTickerNewsError()))
    );

  // delete a ticker news
  @Effect()
  DeleteTickerNews$: Observable<Action> = this.actions$.
  pipe(ofType<ControlActions.DeleteTickerNews>(ControlActions.DELETE_TICKER_NEWS))
    .mergeMap(action =>
      this.newsService.destroyTickerNews(action.payload.ticker_news_id)
        .map((data) => {
          return new ControlActions.DeleteTickerNewsSuccess(data);
        })
        .catch(() => of(new ControlActions.DeleteTickerNewsError()))
    );

  // add a ticker
  @Effect()
  AddTicker$: Observable<Action> = this.actions$.
  pipe(ofType<NewsActions.AddTicker>(ControlActions.ADD_TICKER))
    .mergeMap(action =>
      this.newsService.AddTicker(action.payload)
        .map((data) => {
          return new ControlActions.AddTickerSuccess(data);
        })
        .catch(() => of(new ControlActions.AddTickerError()))
    );
  /*----------------------------------------------------------------------------------------*/
  /*---------------------------------------- Ticker ----------------------------------------*/
  /*-------------------------------------------END------------------------------------------*/
}
