
import { createFeatureSelector, createSelector } from '@ngrx/store';

import * as ControlActions from './control.actions';
import { ControlState } from '../../store/app.state';

import 'rxjs/operator/filter';
import 'rxjs/operator/find';
import * as NewsActions from "../../news/store/news.actions";


export const initialState: ControlState = {
    // news: [],
    // isLoaded: false,
    // // pageState: {currentPage: null, perPage: null, lastPages: null, totalResults: null},
    // lists: [],
    isLoaded: true,
    cgSettings: null,
    newsCgState: null,
    tickers: []
  };

export type Action = ControlActions.All;
export interface ActionWithPayload<T> extends Action {
  payload: T;
}

export function reducer(state = initialState, action: ActionWithPayload<any>): ControlState {
  console.log(state, action);

  switch (action.type) {

    case ControlActions.GET_NEWS_CG_NEWS: {
      return {
        ...state,
        isLoaded: false
      };
    }

    case ControlActions.GET_NEWS_CG_NEWS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        newsCgState: action.payload
      };
    }

    case ControlActions.GET_CG_SETTINGS: {
      return {
        ...state,
        isLoaded: false
      };
    }

    case ControlActions.GET_CG_SETTINGS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        cgSettings: action.payload
      };
    }

    case ControlActions.LOGOUT_CONTROL: {
      return initialState;
    }

    /*----------------------------------------------------------------------------------------*/
    /*---------------------------------------- Ticker ----------------------------------------*/
    /*-----------------------------------------START------------------------------------------*/
    /**
     * ----------------------------------------------------------------------------------
     * ---------------------------------------------------------------------> Ticker News
     * ----------------------------------------------------------------------------------
     */
    /**
     * ---------------------------------------------------------------------> GET TICKERS
     */
    case ControlActions.GET_TICKERS: {
      return {
        ...state,
        isLoaded: false
      };
    }
    case ControlActions.GET_TICKERS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        tickers: action.payload
      };
    }
    case ControlActions.GET_TICKERS_ERROR: {
      return {
        ...state,
        isLoaded: true
      };
    }

    /**
     * ---------------------------------------------------------------------> ADD TICKER NEWS
     */
    case ControlActions.ADD_TICKER_NEWS: {
      return {
        ...state,
        isLoaded: false
      };
    }
    case ControlActions.ADD_TICKER_NEWS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        tickers: action.payload
      };
    }
    case ControlActions.ADD_TICKER_NEWS_ERROR: {
      return {
        ...state,
        isLoaded: true
      };
    }

    /**
     * ---------------------------------------------------------------------> APPROVE TICKER NEWS
     */
    case ControlActions.APPROVE_TICKER_NEWS: {
      return {
        ...state,
        isLoaded: false
      };
    }
    case ControlActions.APPROVE_TICKER_NEWS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        tickers: action.payload
      };
    }
    case ControlActions.APPROVE_TICKER_NEWS_ERROR: {
      return {
        ...state,
        isLoaded: true
      };
    }

    /**
     * ---------------------------------------------------------------------> DISAPPROVE TICKER NEWS
     */
    case ControlActions.DISAPPROVE_TICKER_NEWS: {
      return {
        ...state,
        isLoaded: false
      };
    }
    case ControlActions.DISAPPROVE_TICKER_NEWS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        tickers: action.payload
      };
    }
    case ControlActions.DISAPPROVE_TICKER_NEWS_ERROR: {
      return {
        ...state,
        isLoaded: true
      };
    }

    /**
     * ---------------------------------------------------------------------> DELETE TICKER NEWS
     */
    case ControlActions.DELETE_TICKER_NEWS: {
      return {
        ...state,
        isLoaded: false
      };
    }
    case ControlActions.DELETE_TICKER_NEWS_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        tickers: action.payload
      };
    }
    case ControlActions.DELETE_TICKER_NEWS_ERROR: {
      return {
        ...state,
        isLoaded: true
      };
    }

    /**
     * ---------------------------------------------------------------------> ADD TICKER
     */
    case ControlActions.ADD_TICKER: {
      return {
        ...state,
        isLoaded: false
      };
    }
    case ControlActions.ADD_TICKER_SUCCESS: {
      return {
        ...state,
        isLoaded: true,
        tickers: action.payload
      };
    }
    case ControlActions.ADD_TICKER_ERROR: {
      return {
        ...state,
        isLoaded: true
      };
    }
    /*----------------------------------------------------------------------------------------*/
    /*---------------------------------------- Ticker ----------------------------------------*/
    /*-------------------------------------------END------------------------------------------*/

    default: {
      return state;
    }
  }

}

export const getReviewNewsState = createFeatureSelector<ControlState>('ControlState');
